import React, { useRef, useEffect, useState } from 'react';
import type { InputRef } from 'antd';
import { Input, Select } from 'antd';
import classnames from 'classnames';
import storage from '@/utils/storage';
import {
  DB_SEARCH_TYPE,
  searchTypeMap,
  defaultSearchType,
} from '@/constants';
import styles from './index.less';

const { Option } = Select;

const Search: React.FC = () => {
  const inputRef = useRef<InputRef>(null);
  const _searchType = storage.get(DB_SEARCH_TYPE) || defaultSearchType;
  const [searchType, setSearchType] = useState(_searchType);

  const handleFocusSearch = () => {
    inputRef!.current!.focus({ cursor: 'start' });
  };

  const handlePressEnter = (e: any) => {
    e.stopPropagation();
    window.open(searchTypeMap[searchType].gotoUrl(e.target.value));
  };

  const handleChangeSearchType = (value: string) => {
    setSearchType(value);
    storage.set(DB_SEARCH_TYPE, value);
    handleFocusSearch();
  };

  useEffect(() => {
    handleFocusSearch();
  }, []);

  const selectBefore = () => {
    return (
      <Select
        defaultValue={searchType}
        bordered={false}
        size="large"
        onChange={handleChangeSearchType}
        getPopupContainer={triggerNode => triggerNode.parentNode.parentNode.parentNode}
        popupMatchSelectWidth={false}
        virtual={false}
      >
        {Object.keys(searchTypeMap).map(key => (
          <Option key={key} value={key}>
            <span className={classnames('search-type-icon', styles.searchTypeIcon)}>
              <img src={searchTypeMap[key].logo} />
              <span>{searchTypeMap[key].name}</span>
            </span>
          </Option>
        ))}
      </Select>
    );
  };

  return (
    <Input
      ref={inputRef}
      size="large"
      bordered={false}
      className={styles.searchWrap}
      onPressEnter={handlePressEnter}
      placeholder="输入搜索内容"
      addonBefore={selectBefore()}
    />
  );
};

export default Search;
