import React from 'react';
import { loadMicroApp } from 'qiankun';
import { Spin, Result, Typography } from 'antd';
import { IMicroAppInfo } from '@/microApp';

type IProps = {
  microAppInfo: IMicroAppInfo | null;
}

type IState = {
  initLoading: boolean;
  isError: boolean;
  microAppInfo: IMicroAppInfo;
}

class Panel extends React.Component<IProps, IState>  {
  microApp: any = null;

  constructor(props: any) {
    super(props);
    this.state = {
      initLoading: true,
      isError: false,
      microAppInfo: props.microAppInfo,
    };
  }

  componentDidMount() {
    const { microAppInfo } = this.state;
    if (microAppInfo) {
      this.microApp = loadMicroApp({
        ...microAppInfo,
        container: '#micro-app-container',
      });
    }
    this.microApp.mountPromise.then(() => {
      this.setState({ initLoading: false, microAppInfo });
    }).catch((error: any) => {
      this.setState({ initLoading: false, microAppInfo, isError: true });
    });
  }

  componentWillUnmount() {
    this.microApp.mountPromise.then(() => {
      this.microApp.unmount();
    }).catch(() => { });
  }

  render() {
    const { initLoading, isError, microAppInfo } = this.state;
    return (
      <Spin spinning={initLoading}>
        <div id="micro-app-container" style={{ display: !initLoading && !isError ? 'block' : 'none' }}></div>
        {!initLoading && isError && (
          <div
            style={{
              display: 'flex',
              height: '100%',
              justifyContent: 'center',
              alignItems: 'center',
              flexWrap: 'nowrap',
              paddingBottom: '100px',
            }}
          >
            <Result
              status="error"
              title="抱歉，应用加载失败"
              subTitle={(
                <span>请检查应用是否可运行 <Typography.Link href={microAppInfo?.entry} target="_blank">{microAppInfo?.entry}</Typography.Link></span>
              )}
            >
            </Result>
          </div>
        )}
      </Spin>
    );
  }
}

export default Panel;
