import React from 'react';
import classnames from 'classnames';
import { CalendarOutlined } from '@ant-design/icons';
import { TAppItem } from '@/routes/type';
import { appLayoutMap, weekCNames } from './const';
import { formatDate } from './tool';
import styles from './app.less';

type IProps = {
  item: TAppItem;
  onClick?: any;
};

const Calendar = ({
  item,
  onClick,
}: IProps) => {
  const {
    year, month, day,
    week, firstDayWeek, curDays,
  } = formatDate();
  /**
   * 渲染小组件面板
   */
  const renderCalendarPanel = () => {
    const days = (new Array(firstDayWeek).fill(''))
      .concat(new Array(curDays).fill('').map((i, index) => index + 1))
      .concat(new Array(35 - firstDayWeek - curDays).fill(''));
    return (
      <div className={styles.calendar}>
        <div className={styles.calendarWeek}>
          {weekCNames.map((i, index) => (
            <div key={i}><span>{i}</span></div>
          ))}
        </div>
        <div className={styles.calendarDays}>
          {days.map((i, index) => (
            <div key={`i-${index}`}>
              {i && <span className={classnames(i === day && styles.curDay)}>{i}</span>}
            </div>
          ))}
        </div>
      </div>
    );
  };

  switch (item.size) {
    case appLayoutMap['1x1']:
      return (
        <div className={classnames(styles.container)} onClick={onClick}>
          <div className={classnames(styles.mainPanel, styles.column, styles['jc-center'])}>
            <div className={classnames(styles.f24)}>{day}</div>
            <div className={classnames(styles.f12, styles['tc-theme-color'])}>{`星期${weekCNames[week]}`}</div>
          </div>
        </div>
      );
    case appLayoutMap['1x2']:
      return (
        <div className={classnames(styles.container)} onClick={onClick}>
          <div className={classnames(styles.mainPanel, styles.row, styles['jc-evenly'])}>
            <div className={classnames(styles.f24)}>{`${month}/${day}`}</div>
            <div className={classnames(styles.f16, styles['tc-theme-color'])}>{`星期${weekCNames[week]}`}</div>
          </div>
        </div>
      );
    case appLayoutMap['2x1']:
      return (
        <div className={classnames(styles.container, styles.row)} onClick={onClick}>
          <div className={classnames(styles.mainPanel, styles.column, styles['jc-evenly'])}>
            <div className={classnames(styles.f12)} style={{ lineHeight: '15px', height: '15px' }}>{`${year}/${month}`}</div>
            <div className={classnames(styles.f36)}>{day}</div>
            <div className={classnames(styles.f12, styles['tc-theme-color'])}>{`星期${weekCNames[week]}`}</div>
          </div>
        </div>
      );
    case appLayoutMap['2x4']:
      return (
        <div className={classnames(styles.container)} onClick={onClick}>
          <div className={classnames(styles.mainPanel, styles.column, styles.needSubPanel, styles['jc-between'])} style={{ width: '36%', padding: 8 }}>
            <div className={classnames(styles.iconWrap, styles['jc-end'])}>
              <CalendarOutlined />
            </div>
            <div className={classnames(styles.flex, styles['ai-baseline'], styles['tc-theme-color'], styles['w-full'], styles['jc-start'])}>
              <div className={classnames(styles.f36)} style={{ paddingRight: '4px' }}>{day}</div>
              <div className={classnames(styles.f12)}>{`/ ${month} / ${year}`}</div>
            </div>
          </div>
          <div className={classnames(styles.subPanel)}>
            {renderCalendarPanel()}
          </div>
        </div>
      );
    case appLayoutMap['2x2']:
    default:
      return (
        <div className={classnames(styles.container)} onClick={onClick}>
          <div className={classnames(styles.mainPanel, styles['fw-nowrap'], styles['ac-start'])}>
            <div className={classnames(styles.miniHeader)}>
              <div className={classnames(styles.f12)}>{`${year}年${month}月`}</div>
            </div>
            <div className={classnames(styles.flex, styles['ai-center'], styles['flex-grow'])}>
              {renderCalendarPanel()}
            </div>
          </div>
        </div>
      );
  }
};

export default Calendar;
