import React, { useState, useEffect } from 'react';
import { TAppList, TAppItem } from './type';
import Wallpaper from '@/component/Wallpaper';
import ConfigProvider from '@/component/ConfigProvider';
import Search from '@/component/Search';
import Time from '@/component/Time';
import Tips from '@/component/Tips';
import Apps from '@/component/Apps';
import ContextMenu from '@/component/ContextMenu';
import { defaultAppsList, defaultWorkList } from './constants';
// import { DB_APPS_LIST, IS_DEV } from '@/constants';
// import storage from '@/utils/storage';
import { getQuery } from '@/utils/tool';
import styles from './app.less';

const App: React.FC = () => {
  const { type } = getQuery();
  // const AppsList: TAppList[] = IS_DEV ? defaultAppsList : storage.get(DB_APPS_LIST) || defaultAppsList;
  const AppsList: TAppList[] = (type === 'xhzy' ? defaultWorkList : []).concat(defaultAppsList);
  const [appsList, setAppsList] = useState<TAppList[]>(AppsList.map((i, index) => {
    i.index = index;
    i.items = i.items.map((o, oindex) => {
      o.index = oindex;
      o.pindex = index;
      return o;
    });
    return i;
  }));

  const updateAppsList = (action: string, value: TAppItem) => {
    let nextAppsList = appsList;
    if (action === 'app.update') {
      nextAppsList = appsList.map((i) => {
        i.items = i.items.map((o) => {
          if (value.pindex === i.index && value.index === o.index) {
            return value;
          }
          return o;
        });
        return i;
      });
    }
    setAppsList(nextAppsList);
    // storage.set(DB_APPS_LIST, nextAppsList);
  };

  return (
    <ConfigProvider prefixCls="mytab">
      <main className={styles.main}>
        <Wallpaper />
        <ContextMenu>
          <div className={styles.homePage}>
            <div className={styles.homePageHeaderWrap} />
            <div className={styles.homePageDateWrap}><Time /></div>
            <div className={styles.homePageSearchWrap}><Search /></div>
            <div className={styles.homePageAppGridWrap}>
              <Apps appsList={appsList} updateDate={updateAppsList} />
            </div>
            <div className={styles.homePageTipsWrap}><Tips /></div>
          </div>
        </ContextMenu>
      </main>
    </ConfigProvider>
  );
};

export default App;
