import React from 'react';
import { TAppItem } from '@/routes/type';
import { isLightColor } from '@/utils/tool';
import styles from './index.less';

type IProps = {
  item: TAppItem;
  onClick?: any;
};

const Application = ({
  item,
  onClick
}: IProps) => {
  const gotoUrl = (url: string) => { window.open(url); };
  return (
    <div
      className={styles.appIcon}
      style={{
        backgroundColor: item.backgroundColor || '#c7c7cc',
        color: item.color || isLightColor(item.backgroundColor || '#c7c7cc') ? '#000' : '#fff'
      }}
      onClick={() => (onClick && onClick(item)) || (item.url && gotoUrl(item.url))}
    >
      {item.src ? <img src={item.src} /> : item.iconText || ''}
      {item.tagName && (
        <span
          className={styles.appIconTag}
          style={{
            backgroundColor: item.tagBgColor || '#616161',
            color: item.tagColor || '#fff'
          }}
        >
          {item.tagName}
        </span>
      )}
    </div>
  );
};

export default Application;
